h1 {
  text-align: center;
}

.instrumento {
  width: 100%;
  height: 150px;
  text-align: center;
  border: 1px solid #fca311;
  background: #e5e5e5
}

.instrumentoDestacado {
  background: #fca311;
  border: 1px solid #000000;
  width: 100%;
  height: 150px;
  text-align: center;
  z-index: 100;
}

.letraInstrumento {
  padding-top: 25%;

}

.displayBateria {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 200px);
  justify-content: center;
}



.creditos {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 9px;
}